import React, { Component } from 'react';

import Header from "./HeaderContainer";
import SidebarButton from '../components/header/sidebarButton';
import DetailHeader from "../components/detail/header";
import Comment from "../components/comment";
import api from "../utils/api";
import '../styles/write.scss';
import addImage from '../images/board/btn-plus-pic@2x.png';

const onAttachImg = () => {
  document.getElementById('attachBtn').click();
}

class BoardWriteContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      title: "",
      content: "",
      tag: "",
      images: []
    };
    this.changeHandler = this.changeHandler.bind(this);
    this.postApi = this.postApi.bind(this);
    this.addImage = this.addImage.bind(this);
    this.removeImage = this.removeImage.bind(this);
    this.goBack = this.goBack.bind(this);
  }

  addImage(event) {
    const {images} = this.state;
    if (event.target.files && event.target.files[0]) {
      const reader = new FileReader();  
      reader.onload = (e) => {
        images.push(e.target.result)
        this.setState({ images });
      };
      reader.readAsDataURL(event.target.files[0]);
    }
  }

  changeHandler(e) {
    this.setState({[e.target.name]: e.target.value});
  }

  goBack(){
    const {history} = this.props;
    history.push('/board');
  }

  async postApi() {
    const {title, content, tag, images} = this.state;
    if(!title) {
      swal('제목을 입력해주세요');
      return false;
    }
    if(!content) {
      swal('내용을 입력해주세요');
      return false;
    }
    const postData = {
      title,
      content,
      image: images.length,
    }

    if(tag) postData.tag = tag.replace(/ /g,", ");
    const response = await api('post','/api/v1/community', postData);
    if(!response) return;
    alert('글이 정상적으로 등록되었습니다.');
    this.goBack();
  }

  removeImage(index) {
    const {images} = this.state;
    images.splice(index,1);
    this.setState({images});
  }

  render() {
    const {title, content, tag, image, images} = this.state;
    return (
      <div className="container-wrapper">
        <div className="header-container">
          <div className="back-btn" onClick={this.goBack}>취소</div>
          <div className="title">글쓰기</div>
          <div className={`register-btn ${title && content ? "on" : ""}`} onClick={this.postApi}>등록</div>
        </div>
        <div className="write-container child-container">
          <div className="title content-wrapper">
            <div className="title">제목</div>
            <input className="inputbox" name="title" onChange={this.changeHandler} value={title} placeholder="제목을 입력하세요" />
          </div>
          <div className="tag content-wrapper">
            <div className="title">
              <span className="main">태그</span>
              <span className="sub">공백으로 구분</span>
            </div>
            <input className="inputbox tag" name="tag" onChange={this.changeHandler} value={tag} placeholder="#태그를 입력하세요 (예, 어썸피플 청년강사)" />
          </div>
          <div className="image content-wrapper">
            <div className="title">
              <span className="main">이미지 첨부</span>
              <span className="sub">5M 이하의 파일만 가능</span>
            </div>
            <div className="image-wrapper">
              {
                images.map((item, index) => (
                  <div className="attach-wrapper" key={index}>
                    <div className="close-btn" onClick={() => this.removeImage(index)}>x</div>
                    <img className="attach-img" src={item} />
                  </div>
                ))
              }
              <div className="image-addbox" style={{backgroundImage: `url("${addImage}")`}} onClick={() => onAttachImg()} />
            </div>
            <input id="attachBtn" className="hide-button" type='file' accept="image/*" onChange={this.addImage} />
          </div>
          <div className="content content-wrapper">
            <div className="title">내용</div>
            <textarea className="inputbox" name="content" placeholder="내용을 입력하세요" onChange={this.changeHandler} value={content} rows="8" />
          </div>
        </div>
      </div>
    )
  }
}

export default BoardWriteContainer;
