import axios from "axios";
/* eslint no-use-before-define: "off", no-underscore-dangle: "off" */

const instance = axios.create({
  // baseURL: process.env.NODE_ENV === "development" ? "http://ec2-13-125-107-36.ap-northeast-2.compute.amazonaws.com" 
  // : "https://awesome-school.net",
  baseURL: "http://54.180.189.232/",
  withCredentials: false,
  // validateStatus: status => (status >= 200 && status < 600)
});

const api = async (method, url, data) => {
  try {
    const response = await instance({
      headers: {'Authorization': window.cookieHandler.get("token")},
      method,
      url,
      data,
    });
    console.log('API 응답 : ', response);
    return response;
  } catch(e) {
    const apiMethod = method.toLowerCase();
    if(apiMethod === 'get') {
      alert(`[에러코드 : ${e.response && e.response.status ? e.response.status : '500'}]정보를 가져 올 수 없습니다.`);
    } else if(apiMethod === 'delete') {
      alert(`[에러코드 : ${e.response && e.response.status ? e.response.status : '500'}]정보를 삭제 할 수 없습니다.`);
    } else {
      alert(`[에러코드 : ${e.response && e.response.status ? e.response.status : '500'}]정보를 등록 할 수 없습니다.`);
    }
    return false;
  }
};

export default api;
