import React, { Component } from 'react';

import Header from "./HeaderContainer";
import api from "../utils/api";
import '../styles/report.scss';
import backImg from '../images/global/btn-back@2x.png';


class ReviewContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      participation : false,
      livelihood: false,
      focus: false,
      etc: false,
      mentorComment: "",
    };
    this.goBack = this.goBack.bind(this);
    this.clickOption = this.clickOption.bind(this);
    this.onChangeHandler = this.onChangeHandler.bind(this);
  }

  async componentDidMount() {
  //   const response = await api('get','/api/v1/community');
  //   if(!response) return;
  //   this.setState({boards: response.data.data});
  }

  onChangeHandler(e) {
    this.setState({[e.target.name]:  e.target.value})
  }

  goBack(){
    const {history} = this.props;
    history.goBack();
  }

  clickOption(e) {
    const {[e.target.id] : value} = this.state;
    this.setState({[e.target.id]: !value })
  }

  render() {
    const {participation, livelihood, focus, etc, mentorComment} = this.state;
    return (
      <div className="container-wrapper">
        <div className="header-container">
          <div className="back-btn" onClick={this.goBack}>
            <img src={backImg} />
          </div>
          <div className="title">수업 후 강사 의견</div>
          <div className={`register-btn ${participation || livelihood || focus || etc ? "on" : ""}`} onClick={this.postApi}>등록</div>
        </div>
        <div className="report-container child-container">
          <div className="content">
            <div className="desc">
              <span>수업 피드백 강사의견을 선택해주세요.</span>
              <p className="sub-desc">(중복 선택 가능)</p>
            </div>
            <div className={`review-option ${participation ? "on" : ""}`} onClick={this.clickOption} id="participation">아이들이 프로젝트 활동에 매우 참여적이었다.</div>
            <div className={`review-option ${livelihood ? "on" : ""}`} onClick={this.clickOption} id="livelihood">활기한 수업내용으로 아이들이 즐거워했다.</div>
            <div className={`review-option ${focus ? "on" : ""}`} onClick={this.clickOption} id="focus">수업 분위기가 진지했고 아이들도 집중했다.</div>
            <div className={`review-option ${etc ? "on" : ""}`} onClick={this.clickOption} id="etc">기타 의견</div>
            {
              etc ? (
                <input
                  className="etc-comment"
                  type="text"
                  mentor="mentorComment"
                  onChange={this.onChangeHandler}
                  value={mentorComment}
                />) : null
            }
          </div>
        </div>
      </div>
    )
  }
}

// {
//    "participation" : "true", // "아이들이 프로젝트 활동에~" 선택 시, 아닌 경우 false
//    "livelihood": "true", //"활기찬 수업~" 선택 시, 아닌 경우 false
//    "focus": "true", //"수업 분위기가~" 선택 시, 아닌 경우 false
//    "mentor_comment": "분위기가 어떠어떠했다" //기타
// }

export default ReviewContainer;
