const classTime = (time) => {
  const timeArr = time.split(":").slice(0,2)
  let convertTime;
  if(Number(timeArr[0]) === 12) {
    convertTime = `오후 ${timeArr.join(":")}`
  } else if(Number(timeArr[0]) < 12) {
    convertTime = `오전 ${timeArr.join(":")}`
  } else {
    timeArr[0] -= 12;
    convertTime = `오후 ${timeArr.join(":")}`
  }
  return convertTime;
}

