import React, { Component } from 'react';

import Header from "./HeaderContainer";
import api from "../utils/api";
import '../styles/report.scss';
import backImg from '../images/global/btn-back@2x.png';


class FeedbackContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      feedback: undefined,
    };
    this.goBack = this.goBack.bind(this);
    this.clickOption = this.clickOption.bind(this);
  }

  async componentDidMount() {
    // const response = await api('get','/api/v1/community');
    // if(!response) return;
    // this.setState({boards: response.data.data});
  }

  clickOption() {
    const {feedback} = this.state;
    this.setState({feedback: !feedback })
  }

  goBack(){
    const {history} = this.props;
    history.goBack();
  }

  render() {
    const {feedback} = this.state;
    return (
      <div className="container-wrapper">
        <div className="header-container">
          <div className="back-btn" onClick={this.goBack}>
            <img src={backImg} />
          </div>
          <div className="title">수업 후 강사 의견</div>
          <div className={`register-btn ${feedback !== undefined ? "on" : ""}`} onClick={this.postApi}>등록</div>
        </div>
        <div className="report-container child-container">
          <div className="content">
            <div className="desc">
              수업 피드백 설문을 작성해주세요.
              <br />
              작성할 피드백이 없는 경우 
              <br />
              ‘피드백 없음’ 버튼을 선택해주세요.
            </div>
            <div className={`review-option ${feedback === true ? "on" : ""}`} onClick={this.clickOption}>구글 드라이브에 업로드</div>
            <div className={`review-option ${feedback === false ? "on" : ""}`} onClick={this.clickOption}>피드백 없음</div>
          </div>
        </div>
      </div>
    )
  }
}

export default FeedbackContainer;
