import React, { Component } from 'react';
import Header from "./HeaderContainer";

import api from "../utils/api";
import '../styles/report.scss';
import backImg from '../images/global/btn-back@2x.png';
import addPlusImage from '../images/board/btn-plus-pic@2x.png';

const onAttachImg = () => {
  document.getElementById('attachBtn').click();
}

class ReviewContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      image: false,
      images: [],
    };
    this.goBack = this.goBack.bind(this);
    this.addImage = this.addImage.bind(this);
    this.onBlurHandler = this.onBlurHandler.bind(this);
    this.onChangeHandler = this.onChangeHandler.bind(this);
  }

  async componentDidMount() {
    // const response = await api('get','/api/v1/community');
    // if(!response) return;
    // this.setState({boards: response.data.data});
  }

  onBlurHandler(e) {
    if(!e.target.value) this.setState({[e.target.name]: 0});
  }

  onChangeHandler(e) {
    const onlyNumber = /^(\d|[^-]-)+$/;
    let targetValue = e.target.value;
    if(targetValue) {
      if(onlyNumber.test(e.target.value)) {
        while (targetValue[0] === '0' && targetValue.length > 1) {
          targetValue = targetValue.slice(1, targetValue.length);
        }
        this.setState({[e.target.name]: targetValue});
      } else {
        alert('번호만 입력해주세요')
      }
    } else {
      this.setState({[e.target.name]: targetValue});
    }
  }

  goBack(){
    const {history} = this.props;
    history.goBack();
  }

  async addImage(event) {
    event.persist()
    const {images} = this.state;
    if (event.target.files && event.target.files[0]) {
      for(let i=0; i<event.target.files.length; i++) {
        const reader = new FileReader();
        reader.onload = async (e) => {
          await images.push(e.target.result);
          this.setState({ images });
        };
        reader.readAsDataURL(event.target.files[i]);
      }
    }
  }

  render() {
    const {image, images} = this.state;
    return (
      <div className="container-wrapper">
        <div className="header-container">
          <div className="back-btn" onClick={this.goBack}>취소</div>
          <div className="title">활동 사진</div>
        </div>
        <div className="report-container child-container">
          <div className="content">
            <div className="desc">
              <span>활동 사진을 등록해주세요.</span>
            </div>
            <div className="image-wrapper">
              {
                images.map((item, index) => (
                  <div className='image-addbox on' key={index} name='image' style={{backgroundImage: `url("${item}")`}} />
                ))
              }
              <div className='image-addbox' name='image' style={{backgroundImage: `url("${addPlusImage}")`}} onClick={() => onAttachImg()} />
              <input id='attachBtn' className="hide-button displaynone" name='image' type='file' accept="image/*" multiple onChange={this.addImage} />
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default ReviewContainer;
